<template>
    <!--    -->
    <div class="asyncTest">
        <h1>2020429</h1>
        <h5></h5>
    </div>
</template>

<script>
	const moment = require('moment')
	export default {
		name: "asyncTest",
		components: {},
		data() {
			return {}
		},
		methods: {
			asyncFun() {
				return new Promise((resolve) => {
					console.log('1', '', moment().format('YYYY-MM-DD hh:mm:ss'))
					setTimeout(() => {
						console.log('2', '', moment().format('YYYY-MM-DD hh:mm:ss'))
						resolve()
					}, 1000)
					console.log('3', '', moment().format('YYYY-MM-DD hh:mm:ss'))
				})
			},
			async run() {
				await this.asyncFun()
			}
		},
		computed: {},
		created: function () {
			this.run()
		}
	}
</script>

<style lang="scss">
    .asyncTest {
    }
</style>
